    text-decoration: none;
  }
  .connect-wallet-button {
    background: #42b51f;
    background: linear-gradient(180deg, #42b51f, #049702);
    border-radius: 50px;
    display:inline-block;
    padding: 13px 32px;
  }
  .banner-section 
