    font-size: 16px;
    font-weight: 400;
    padding-bottom: 15px;
  }
  .whitepaper-btn {
    background: #1f2f5c;
    border: 2px solid #b4b9c8;
    border-radius: 50px;
    color: #fff;
    display:inline-block;
    font-size: 14px;
    font-weight: 600;
    line-height: 14px;
    padding: 13px 34px;
    text-decoration: none;
    text-transform: uppercase;
  }
  .banner-left-bottom-con 
  {
