    margin-right: 20px;
    margin-top: 1px;
  }
  .banner-bottom-img-1 
  {
    position: relative;
  }
  .banner-bottom-img-1:after {
    background-image: url(../../presale_assets/banner-bottom-devider.png);
    background-position: 0 0;
    content: "";
    height: 69px;
    position: absolute;
    right: -9px;
    top: -10px;
    width: 1px;
  }
  .banner-right-con 
  {
    flex:0 0 370px;
  }
  .banner-right-con h2 {
    background: url(../../presale_assets/banner-right-heading-bg.png) 0 0 no-repeat;
