    font-size: 18px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
  }
  .pre-sale-info {
    background: #fff;
    margin-top: 4px;
    padding: 0 20px 20px;
    position: relative;
  }
  .pre-sale-info:after {
