  p#timer
  {
      width:100%;
      display:flex;
      justify-content: center;
      align-items:center;
  }
  p#timer div 
  {
      flex:0 0 72px;
      height: 72px;
      background: url(../../presale_assets/timer-bg.png) left top no-repeat;
      text-align: center;
      margin-right: 10px;
      font-size: 28px;
      line-height:35px;
      color:#fff;
      font-weight: 700;
      font-family: 'Inter', sans-serif;
      padding-top:10px;
      position:relative;
  }
  p#timer div::after
  {
      content:":";
      position: absolute;
      right:-9px;
      top:16px;
      color:#737f8b;
      font-size:22px;
  
  } 
  p#timer div:last-child::after 
  {
      display:none;
  }
  p#timer div:last-child
  {
      margin:0;
  }
  p#timer div span {
    display:block;
    font-size: 12px;
    line-height:12px;
    text-transform: uppercase;
    width: 100%;
  }
  .copy-outer {
   display:inline-block;
    position: relative;
    width: 100%;
  }
  .copy-button {
    background: #004090;
    background: linear-gradient(180deg, #004090, #1b1275);
    border: 0;
    border-radius: 50px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    height: 36px;
    letter-spacing: 1px;
    line-height: 38px;
    padding: 0 19px;
    position: absolute;
    right: 0;
    text-transform: uppercase;
    top: -18px;
  }
  .tooltip-code 
  {
      display: block;
      position: relative;
      margin-top: -28px;
  }
  .tooltip-code .tooltiptext {
    background-color: #1f2f5c;
    border-radius: 6px;
    bottom: 150%;
    color: #fff;
    font-family: Inter, sans-serif;
    font-size: 12px;
    font-weight: 400;
    left: 50%;
    line-height: 15px;
    margin-left: -75px;
    opacity: 0;
    padding: 5px;
    position: absolute;
    text-align: center;
    text-transform: none;
    transition: opacity 0.3s;
    visibility: hidden;
    width: 140px;
    z-index: 1;
  }
  .tooltip-code .tooltiptext:after {
    border: 5px solid transparent;
    border-top-color: #1f2f5c;
    content: "";
    left: 50%;
    margin-left: -5px;
    position: absolute;
    top: 100%;
  }
  .tooltip-code:hover .tooltiptext {
    opacity: 1;
    visibility: visible;
  }
